export default {
	data() {
		return {
			key: '',
			mark: false,
			showImg: '',
			imgs: [],
			orderId: '',
			repairData: ''
		};
	},
	computed: {
		baseImgUrls() {
			return this.$store.state.baseImgUrl;
		}
	},
	methods: {
		moveHandle() {},
		closeMark() {
			this.mark = !this.mark;
		},
		//图片预览
		showImgs(urlImg) {
			this.mark = !this.mark;
			this.showImg = this.baseImgUrls + urlImg;
		},
		getRepairDetail() {
			let data = {
				id: this.orderId
			};
		},
		evaluate() {
			if(this.repairData.id){
				this.$Router.push({ name: 'evaluate',params: { id: this.repairData.id } });
			}
		}
	},
	onLoad(option) {
		this.orderId = option.data;
		this.getRepairDetail();
	},
	onShow() {
		if(this.orderId){
			this.getRepairDetail();
		}
	}
};
